









































import { State } from 'vuex-class';
import { IHistoryExam, IFileUrl } from '@/types/interface';
import { getUploadFileUrl } from '@/utility';
import { Component, Vue } from 'vue-property-decorator';
import banner from '../../components/Banner.vue';

@Component({
  components: {
    banner,
  },
})

export default class HistoryTestList extends Vue {
  @State('historyExamList', { namespace: 'pastTest' }) stateHistoryExamList!: IHistoryExam[];

  testItemList: IHistoryExam[] = [];

  link() {
    this.$router.push('/test/history');
  }

  created() {
    this.testItemList = this.stateHistoryExamList;
  }

  getQuestion(item: IHistoryExam) {
    if (item.flagQ !== 1) return;
    getUploadFileUrl({
      // eslint-disable-next-line @typescript-eslint/camelcase
      exam_no: item.id,
      type: '1',
    })
    .then((data: IFileUrl[]) => {
      // const link = document.createElement('a');
      // link.href = data[0].url;
      // link.download = '';
      // link.target = '_blank';
      // link.click();
      window.open(data[0].url, '_blank');
    });
  }

  getAnswer(item: IHistoryExam) {
    if (item.flagA !== 1) return;
    getUploadFileUrl({
      // eslint-disable-next-line @typescript-eslint/camelcase
      exam_no: item.id,
      type: '2',
    })
    .then((data: IFileUrl[]) => {
      // const link = document.createElement('a');
      // link.href = data[0].url;
      // link.download = '';
      // link.target = '_blank';
      // link.click();
      window.open(data[0].url, '_blank');
    });
  }
}
